@import url("https://fonts.googleapis.com/css?family=Comic+Sans+MS");
@import url("https://fonts.googleapis.com/css?family=Megrim");
.button-holder {
  display: inline-block;
  margin-left: auto;
  margin-right: auto; }

.intro-response {
  font-size: 6em;
  font-weight: bold; }

.button {
  background-color: #212121;
  color: #FAFAFA;
  font-weight: bold;
  text-align: center;
  font-size: large;
  border: 2px solid #212121;
  border-radius: 8px;
  text-transform: uppercase;
  padding: 10px 15px;
  margin: 5px;
  display: inline-block;
  cursor: pointer;
  -webkit-transition: font-size 0.5s ease, background-color 0.5s ease-out;
  -o-transition: font-size 0.5s ease, background-color 0.5s ease-out;
  transition: font-size 0.5s ease, background-color 0.5s ease-out; }
  .button:hover {
    font-size: x-large;
    background-color: #424242;
    border: 2px solid #424242; }

.no-show {
  opacity: 0; }

.movement {
  background: #81d4fa url(../assets/move.svg) repeat 0 0;
  background-position: 40% 50%;
  background-clip: text;
  font-weight: 800;
  font-size: 6em;
  text-transform: uppercase;
  cursor: default;
  -webkit-animation: slide 20s linear infinite;
          animation: slide 20s linear infinite;
  -webkit-transition: color 1s ease-in, text-shadow 1s ease-in, opacity 1s ease-in;
  -o-transition: color 1s ease-in, text-shadow 1s ease-in, opacity 1s ease-in;
  transition: color 1s ease-in, text-shadow 1s ease-in, opacity 1s ease-in; }

@-webkit-keyframes slide {
  from {
    background-position: -400px 0; }
  to {
    background-position: 0 0; } }

@keyframes slide {
  from {
    background-position: -400px 0; }
  to {
    background-position: 0 0; } }

.annoyance {
  color: #212121;
  font-weight: 800;
  font-size: 6em;
  text-transform: lowercase;
  cursor: default;
  -webkit-transition: opacity 1s ease-in;
  -o-transition: opacity 1s ease-in;
  transition: opacity 1s ease-in; }

.pops {
  color: #FDD835;
  font-family: 'Megrim', cursive;
  text-transform: uppercase;
  font-size: 6em;
  cursor: default; }

.white-txt {
  color: white; }

.blackout {
  background: #212121; }

.trans-text {
  color: transparent; }

.shadow {
  text-shadow: 10px 10px 0px #212121; }

.comic-san {
  font-family: 'Comic Sans MS', cursive; }

.glow {
  -webkit-animation: neon 1.5s ease-in-out infinite alternate;
          animation: neon 1.5s ease-in-out infinite alternate; }

@-webkit-keyframes neon {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px  #fff, 0 0 30px  #fff, 0 0 40px  #FDD835, 0 0 70px  #FDD835, 0 0 80px  #FDD835, 0 0 100px #FDD835, 0 0 150px #FDD835; }
  to {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #FDD835, 0 0 35px #FDD835, 0 0 40px #FDD835, 0 0 50px #FDD835, 0 0 75px #FDD835; } }

@keyframes neon {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px  #fff, 0 0 30px  #fff, 0 0 40px  #FDD835, 0 0 70px  #FDD835, 0 0 80px  #FDD835, 0 0 100px #FDD835, 0 0 150px #FDD835; }
  to {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #FDD835, 0 0 35px #FDD835, 0 0 40px #FDD835, 0 0 50px #FDD835, 0 0 75px #FDD835; } }

/*The code for glitch effect: https://gist.github.com/idmontie/b54716b99ff5f5345234*/
.glitch {
  position: relative; }
  .glitch:after {
    -webkit-animation: glitch-animation 2s infinite linear alternate-reverse;
            animation: glitch-animation 2s infinite linear alternate-reverse;
    background: #fafafa;
    clip: rect(0, 900px, 0, 0);
    color: #212121;
    content: attr(data-text);
    left: 2px;
    overflow: hidden;
    position: absolute;
    text-shadow: -1px 0 red;
    top: 0; }
  .glitch:before {
    -webkit-animation: glitch-animation-2 3s infinite linear alternate-reverse;
            animation: glitch-animation-2 3s infinite linear alternate-reverse;
    background: #fafafa;
    clip: rect(0, 900px, 0, 0);
    color: #212121;
    content: attr(data-text);
    left: -2px;
    overflow: hidden;
    position: absolute;
    text-shadow: 1px 0 blue;
    top: 0; }

